

import './reset.css'
import './defaults.css'
import './range.css'
import './App.css'

import ReactPlayer from '../index'
import React from 'react'
import { hot } from 'react-hot-loader'

import { useState } from "react"

export const App = () => {
  const [ready, setReady] = useState(false);
  return (
    <>
      <ReactPlayer
        muted={true}
        width='100%'
        height='100%'
        config={{
          youtube: {
            playerVars: {
              autoplay: 1,
              showinfo: 0,
              controls:1,
              modestbranding: 1,
              hl: true ? 'en' : 'zh'
            },
          },
        }}
        onReady={() => {
          console.log("play ready")
          setReady(true);
        }}
        onError={(e) => {
          console.log("play error", e)
        }}
        controls={true}
        url={window.location.search.slice(1)} />
      {!ready && <div className="videoLoading">
      </div>
      }
    </>
  )
}
export default hot(module)(App)
